import React, { Component } from "react";
import "./skill.css";

class SkillHolder extends Component {
    //skillType
    //0: mostly work with
    //1: want to learn
    state = { skillType: 0 };

    renderBtnStyle = int => {
        if (int === this.state.skillType) {
            return "circle-btn btn-dark";
        } else {
            return "circle-btn btn-light";
        }
    };

    renderContent = () => {
        if (this.state.skillType === 0) {
            return (
                <div style={{ lineHeight: "2.0", padding: "5px" }}>
                    <h3>I am working with</h3>
                    <div>
                        React-Redux
                        <br />
                        Node JS, Express, Redis, Jest
                        <br />
                        Mongo DB, SQL Database
                        <br />
                        HTML, CSS, JavaScript
                        <br />
                        Bootstrap, Semantic UI
                        <br />
                        Heroku, Atlas
                        <br />
                        C++, Java, Object Oriented Programming
                    </div>
                </div>
            );
        } else if (this.state.skillType === 1) {
            return (
                <div style={{ lineHeight: "2.0", padding: "5px" }}>
                    <h4>I want to learn</h4>
                    <div>
                        Azure || Google Cloud Service || AWS
                        <br />
                        GoLang, Python
                        <br />
                        Svelt(front-end framwork)
                        <br /> Flutter (mobile cross-platform development kit)
                        <br />
                        Deno (run time environment for js in backend, created by
                        NodeJs creator)
                    </div>
                </div>
            );
        }
    };

    render() {
        return (
            <React.Fragment>
                <div className="flex-box">
                    <button
                        onClick={() => this.setState({ skillType: 0 })}
                        className={this.renderBtnStyle(0) + " zoom-on-hover"}
                    >
                        <i className="fas fa-cog fa-spin" />
                    </button>
                    <button
                        onClick={() => this.setState({ skillType: 1 })}
                        className={this.renderBtnStyle(1) + " zoom-on-hover"}
                    >
                        <i className="fas fa-question" />
                    </button>
                </div>

                <div className="skill-detail">{this.renderContent()}</div>
            </React.Fragment>
        );
    }
}

export default SkillHolder;
